.App {
  /* text-align: center; */
  color:#fff;
  height:100vh;
  width:100vw;
  overflow: hidden;
  background-color: #000;
}
.container{
  padding-left:62px;
  color:#222;
  width:100%;
  box-sizing: border-box;
  height: calc(100% - 85px);
  overflow-y:auto;
  overflow-x:hidden;
}
section{
  width:100%;
  min-height:100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  padding:20px 0;
  
}
.section-title{
    color:#fff;
    padding:15px;
    background:linear-gradient(to bottom right, #090909,#111);
    margin:0;
}
.wrapper {
  position: relative;
  background: #333333bb;
  padding: 20px;
}
.social-links{
  display: flex;
  justify-content: space-around;
  width:100%;  
}
.social-links.small{
  padding:0 20px;
  max-width:100px;
}
a{
  color:#fff;
}
a:hover{
  color:#ddd;
}
