#contact {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: #fff;
}
#contact a {
  color: #ddd;
}
#contact a:hover {
  color: #fff;
}
#contact img {
  vertical-align: middle;
}
#contact * {
  box-sizing: border-box;
}
#contact form {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  align-items: center;
}
#contact label {
  width: 30%;
  text-align: right;
  position: relative;
  box-sizing: content-box;
  margin-left: -20px;
}
#contact label:hover {
  cursor: pointer;
}
#contact input,
#contact textarea {
  all: unset;
}
#contact label,
#contact input,
#contact textarea {
  margin-top: 10px;
  margin-bottom: 10px;
}
#contact input,
#contact textarea {
  margin-left: 10px;
  padding: 5px 5px 0 5px;
}
#contact textarea {
  width: 100%;
  background: #33333399;
  max-height: 500px;
  word-wrap: break-word;
}
#contact input[type="text"],
#contact input[type="email"] {
  border-bottom: 3px solid #fff;
  width: 100%;
}
#contact textarea::placeholder,
#contact input::placeholder {
  color: #eee;
  font-size: 15px;
}
#contact input[type="submit"] {
  width: 100%;
  text-align: center;
  background: #5594b9;
  padding-bottom: 5px;
}
#contact input[type="submit"]:hover {
  cursor: pointer;
  background: #679fc0;
}
