header {
  display: flex;
  padding: 10px;
  align-items: center;
  height: 65px;
  background-image: url("../../imgs/header-bg.jpg"), linear-gradient(to top, #000, 80%, rgba(0, 0, 0, 0));
  background-attachment: local;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: multiply;
}
header.sticky-top {
  position: sticky;
  top: 0;
}
header.left {
  justify-content: flex-end;
}
header.right {
  justify-content: flex-start;
}
header.center {
  justify-content: center;
}
header h1,
header h2,
header h3,
header h4,
header h5,
header h6 {
  margin: unset;
  padding: 0 20px;
}
