#about {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}
#about .wrapper {
  max-width: 800px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  background: #333333dd;
}
#about p {
  flex-basis: 70%;
  padding: 0 20px;
  text-align: center;
}
#about .separator {
  height: 2px;
  width: 100%;
  background: #fff;
  flex-grow: 0;
  flex-shrink: 0;
  margin: 10px 0;
}
