nav li {
  height: 70px;
  width: inherit;
}
nav a {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  text-decoration: none;
  width: inherit;
  height: inherit;
  transform-origin: left;
  transition: transform 500ms ease-in-out;
}
nav a > div {
  transform: none;
  transition: transform 500ms ease-in-out;
  transition-delay: 0s;
}
nav a .nav-link-text {
  position: absolute;
  display: block;
  opacity: 0;
  transform: translate(25%, -40px);
  transition: opacity 500ms ease-in-out;
  font-weight: 700;
  transition-delay: 0s;
}
nav a i {
  transition: opacity 500ms ease-in-out, font-size 500ms ease-in-out;
  transition-delay: 500ms;
}
nav ul {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: inherit;
  margin: 0;
  padding: 0;
  transform: translateX(32%);
  transition: transform 500ms ease-in-out;
}
nav.show a > div {
  transform: translateX(-30%);
  transition-delay: 300ms;
}
nav.show a .nav-link-text {
  opacity: 1;
  transition-delay: 500ms;
}
nav.show a i {
  opacity: 0.25;
  transition-delay: 0s;
}
nav.show ul {
  transform: none;
}
nav a:hover {
  color: #b3cdff;
  background: rgba(0, 0, 0, 0.815);
}
nav a:hover .underline {
  position: absolute;
  width: 100%;
  height: 3px;
  background: #ddd;
  left: 2px;
  bottom: -5px;
  animation: slide-right 3s infinite;
  transition: transform 500ms ease-in-out;
}
nav a:hover i {
  color: #fff;
}
nav a.active {
  color: #b3cdff;
  background: rgba(0, 0, 0, 0.815);
}
nav a.active i {
  color: #fff;
}
nav:hover {
  transform: perspective(450px) rotateY(5deg) rotateZ(-1deg) translateX(-110px);
}
@keyframes slide-right {
  0% {
    transform: scaleX(0);
    transform-origin: left;
  }
  25% {
    transform: scaleX(1);
    transform-origin: left;
  }
  26% {
    transform-origin: right;
  }
  50%,
  100% {
    transform: scaleX(0);
    transform-origin: right;
  }
}
nav {
  position: relative;
  will-change: transform;
  display: flex;
  width: 180px;
  transform: translateX(-65%);
  transition: transform 500ms ease-out, opacity 500ms ease-out;
  overflow: hidden;
  box-shadow: 1px 0px 15px #333;
  z-index: 1000;
  mask-image: linear-gradient(to right, #fff, 90%, #ffffff7c);
  background-image: url("../../imgs/nav-bg.jpg");
  background-attachment: local;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
nav ul {
  max-height: 500px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 50px 0;
  box-sizing: border-box;
}
nav.sticky-left {
  position: fixed;
  height: calc(100% - 85px);
}
nav.show {
  transform: none;
}
nav li {
  height: 100px;
  list-style: none;
  cursor: pointer;
}
nav .nav-link-text {
  font-size: 20px;
}
nav:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient(to bottom right, #000, #1f1f1f);
  opacity: 0.8;
  z-index: -1;
}
.menu-toggle {
  display: inline-block;
  position: absolute;
  cursor: pointer;
  right: 10px;
  top: 0;
  z-index: 1000;
}
.bar1,
.bar2,
.bar3 {
  width: 35px;
  height: 5px;
  background-color: #fff;
  margin: 6px 0;
  transition: 0.4s;
}
nav.show .bar1 {
  transform: rotate(-45deg) translate(-9px, 6px);
  will-change: transform;
}
nav.show .bar2 {
  opacity: 0;
  will-change: transform;
}
nav.show .bar3 {
  transform: rotate(45deg) translate(-8px, -8px);
  will-change: transform;
}
